// Middleware to configure next-auth public routes
// By default, next-auth protects all routes. This file makes certain routes public.

export { default } from "next-auth/middleware";

export const config = {
  // Public routes that don't require authentication
  matcher: [
    // Allow home page
    "/",
    // Allow API routes for products, categories, cart, etc.
    "/api/products",
    "/api/categories",
    "/api/coupons",
    "/api/coupons/verify",
    "/api/cart/items",
    "/api/cart/clear",
    "/api/search",
    // Allow static files
    "/_next",
    "/favicon",
  ],
};
